import dev from './config.dev';
import sk from './config.sk';
import cz from './config.cz';
import at from './config.at';
import pl from './config.pl';
import test from './config.test';

const config = {
  imageSize: 1080,
  thumbnailSize: 320,
  avatarSize: 168,
}

const getConfig = () => {
  if(process.env.NODE_ENV === 'development') {
    return dev;
  }

  switch(process.env.REACT_APP_HOMEPAGE) {
    case 'https://dobysk.krdeldatlov.sk':
      return sk;

    case 'https://doby.krdeldatlov.sk':
      return sk;

    case 'https://dobyat.krdeldatlov.sk':
      return at;
    
    case 'https://dobycz.krdeldatlov.sk':   
      return cz;
      
    case 'https://dobypl.krdeldatlov.sk': 
      return pl;

    default:
      return test;
  }
}

const c = { ...config, ...getConfig() };
export default c;